<template>
  <configure-cron-form
    :valid="valid"
    :error="error"
    :onSave="onSave"
    :minutes="minutes"
    :onCancel="onCancel"
    :onDelete="onDelete"
    :isInitialized="isInitialized"
    displayName="BIOS Services Reboot"
    v-on:updateValid="valid = $event"
    v-on:updateMinutes="minutes = $event"
    :isFetching="isFetching"
  ></configure-cron-form>
</template>
<script>
import _get from 'lodash/get';
import ConfigureCronForm from '../Common/ConfigureCronForm.vue';
import * as rubixService from '../../services/rubixService';
import * as slaveAppsService from '../../services/slaveAppsService';

export default {
  name: 'configure-bios-cron-view',
  props: ['onCancel', 'sync', 'selectedService', 'isBulk'],
  components: { ConfigureCronForm },
  data() {
    return {
      valid: false,
      minutes: 0,
      service: {},
      error: {
        message: null,
      },
      isInitialized: false,
      isFetching: false,
    };
  },
  mounted() {
    this.fetchBiosCronDetails();
  },
  methods: {
    fetchBiosCronDetails() {
      if (this.selectedService) {
        this.service = this.selectedService;
      }
      this.fetchRestartJob();
    },
    initializeCronView() {
      if (this.isBulk) {
        this.minutes = 0;
        this.error.message = null;
        this.isInitialized = true;
        return;
      }
      this.minutes = _get(this.service, 'timer', 0);
      this.error.message = _get(this.service, 'error', null);
      this.isInitialized = true;
    },
    getApiService() {
      if (this.isBulk) {
        return {
          service: {
            createRestartJob: slaveAppsService.bulkUpdateSystemRebootJob,
            deleteRestartJob: slaveAppsService.bulkDeleteSystemRebootJob,
            fetchRestartJob: () => Promise.resolve(true),
          },
        };
      }
      if (this.selectedService) {
        return {
          service: {
            createRestartJob: slaveAppsService.updateSystemRebootJob,
            deleteRestartJob: slaveAppsService.deleteSystemRebootJob,
            fetchRestartJob: slaveAppsService.fetchSystemRebootJob,
          },
        };
      }
      return {
        service: {
          createRestartJob: rubixService.updateSystemRebootJob,
          deleteRestartJob: rubixService.deleteSystemRebootJob,
          fetchRestartJob: rubixService.fetchSystemRebootJob,
        },
      };
    },
    getSlaveId() {
      if (this.isBulk) {
        return this.selectedService.map(service => _get(service, 'slaveId', null)).filter(val => val);
      }
      return _get(this.selectedService, 'slaveId', null);
    },
    fetchRestartJob() {
      const { service } = this.getApiService();
      service
        .fetchRestartJob(this.getSlaveId())
        .then(data => {
          this.service = data;
          this.initializeCronView();
        })
        .finally(() => {
          this.isFetching = false;
        });
    },
    onSave() {
      const payload = {
        timer: +this.minutes || 0,
        service: this.service.service,
      };
      const { service } = this.getApiService();
      service.createRestartJob(payload, this.getSlaveId()).then(() => {
        this.$toasted.show('Successfully updated');
        this.onCancel();
        this.syncDetails();
      });
    },
    syncDetails() {
      if (typeof this.sync === 'function') {
        this.sync();
      }
    },
    onDelete() {
      const { service } = this.getApiService();

      service.deleteRestartJob(this.getSlaveId()).then(() => {
        this.$toasted.show('Successfully deleted');
        this.onCancel();
        this.syncDetails();
      });
    },
  },
};
</script>
