import http from '../axios';

const endpoint = '/slaves-bios-apps';

export const fetchApps = () => {
  return http.get(`${endpoint}`).then(slaveApps => {
    const data = Object.keys(slaveApps).reduce((acc, curr) => {
      if (slaveApps[curr] && !slaveApps[curr].error) {
        acc[curr] = slaveApps[curr].data;
      }
      return acc;
    }, {});
    return data;
  });
};

export const mergeSlaveBiosAppDetails = (slaveBiosApps = {}, slaveDevices = {}) => {
  return Object.keys(slaveBiosApps).map(slaveUUID => ({
    slaveId: slaveUUID,
    ...slaveBiosApps[slaveUUID],
    ...(slaveDevices[slaveUUID] ? slaveDevices[slaveUUID] : {}),
  }));
};

export const updateLatest = slaveUUID => {
  return http.put(`${endpoint}/update-latest/${slaveUUID}`);
};

export const updateToken = (slaveUUID, data) => {
  return http.post(`${endpoint}/update-token/${slaveUUID}`, data);
};

export const restartBios = slaveUUID => {
  return http.post(`${endpoint}/restart-bios/${slaveUUID}`, {}, { restrictToast: true });
};

export const bulkUpdateAll = slaveUUIDs => {
  return http.post(`${endpoint}/bulk-update-latest`, { uuids: slaveUUIDs });
};

export const bulkUpdateToken = (slaveUUIDs, token) => {
  return http.post(`${endpoint}/bulk-update-token`, { uuids: slaveUUIDs, token });
};

export const bulkRestartBios = slaveUUIDs => {
  return http.post(`${endpoint}/bulk-restart-bios`, { uuids: slaveUUIDs });
};
