<template>
  <v-card dense v-if="isStatusAvailable">
    <v-card-title>{{ title }}</v-card-title>
    <v-card-text>
      <v-list dense>
        <v-list-item v-for="item in items" :key="item.name" :prepend-icon="item.icon" no-action>
          <v-list-item-icon>
            <v-icon color="orange"> {{ item.icon }} </v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="orange--text" v-text="item.name"></v-list-item-title>
            <v-list-item-subtitle v-text="item.message"></v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card-text>
  </v-card>
</template>
<script>
/*
operation display format
[{
  action: 'restart' | 'update',
  message: '',
  type: 'error',
  name: '' // can be device name or anything
}]
*/
const ICON_MAP = {
  restart: 'fa-repeat',
  update: 'fa-check-circle-o',
  default: 'fa-exclamation-circle',
  upgrade: 'fa-check-circle-o',
};

export default {
  name: 'bulk-apps-operation-status',
  props: ['operationStatuses', 'title'],
  computed: {
    isStatusAvailable() {
      return this.operationStatuses.length > 0;
    },
    items() {
      return this.operationStatuses.map(item => ({ ...item, icon: ICON_MAP[item.action] }));
    },
  },
};
</script>
