function validateIpAddress(ipaddress) {
  if (
    /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(
      ipaddress,
    ) ||
    /(?:[a-z0-9](?:[a-z0-9-]{0,61}[a-z0-9])?\.)+[a-z0-9][a-z0-9-]{0,61}[a-z0-9]/g.test(ipaddress)
  ) {
    return true;
  }

  if (`${ipaddress}`.toLowerCase() === 'localhost') {
    return true;
  }

  return false;
}

function isNotNumber(number) {
  return isNaN(+number);
}

export function isJsonString(str) {
  try {
    JSON.parse(str);
  } catch (e) {
    return false;
  }
  return true;
}

export default {
  maxLengthValidator: len => value =>
    (typeof value === 'string' && value.length <= len) || `Value should be of ${len} length.`,
  minLengthValidator: len => value => (typeof value === 'string' && value.length >= len || `Value should be of at least ${len} length.`),
  required: value => !!value || 'Required',
  number: value => !isNotNumber(value) || 'Value should be a number',
  isIpAddress: value => !!validateIpAddress(value) || 'Invalid IP Address',
  isJsonString: value => isJsonString(value) || 'Data should be in a valid JSON format',
};
